import { tab } from '@testing-library/user-event/dist/tab';
import { useState,useEffect } from 'react';

const todoList = [
    {
        id:1,
        content:'把冰箱發霉的檸檬拿去丟',
        state:'checked'
    },{
        id:2,
        content:'打電話叫媽媽匯款給我',
        state:''
    },{
        id:3,
        content:'整理電腦資料夾',
        state:'checked'
    },{
        id:4,
        content:'繳電費水費瓦斯費',
        state:''
    },{
        id:5,
        content:'約vicky禮拜三泡溫泉',
        state:''
    },{
        id:6,
        content:'約ada禮拜四吃晚餐',
        state:''
    }
];
const tabsList = [
    {name:'全部',className:'active'},
    {name:'待完成',className:''},
    {name:'已完成',className:''}
];

const Input = (props) =>{
    const { todo, setTodo, addTodo, setAddTodo , tabs, setTabs  } = props;
    const newitem = async() =>{
        await setTodo([...todo,{id:Date.now(),content:addTodo,state:''}]);
        await setAddTodo('');
        await setTabs(tabs.filter(item=>{
            if(item.name === '全部'){
                return (item.className = 'active', item);
            }else{
                return (item.className = '', item)
            }
        }));
    }
    return(
        <div className="card input">
            <input type="text" placeholder="新增代辦事項" className="txt" value={addTodo} onChange={e=>setAddTodo(e.target.value)}/>
            <input type="button" className="add-btn" onClick={e=>{if(addTodo.trim()===''){alert('請輸入代辦')}else{newitem()}}}/>
        </div>
    )
};


const Todo = (props) =>{
    const { tabs, setTabs, todo, setTodo } = props;
    const [ filterList, setFilterList ] = useState([]);
    const detitem = async(id) =>{
        await setTodo(todo.filter(item=>item.id !== id));
    }
    
    useEffect(() =>{
        tabs.filter(item =>{
            if(item.className === 'active'){
                item.name === '全部' ? setFilterList(todo) : item.name === '待完成'? setFilterList(todo.filter(item=>item.state === '')) : setFilterList(todo.filter(item=>item.state === 'checked'))
            }});
    },[tabs,todo])

    const Tabs = () =>{
        return(
            tabs.map((item,index)=><li key={index} className={item.className} onClick={e=>changeTab(e)}>{item.name}</li>)
        )
    }
    const TodoItem = () =>{
        if(todo.length === 0 ){
            return (
                <div className="notodo d-flex flex-column align-items-center mt-5">
                    <p>目前尚無待辦事項</p>
                    <img src="https://github.com/hexschool/webLayoutTraining1st/blob/master/%E5%85%AC%E7%9B%8A%E9%AB%94%E9%A9%97%E7%87%9F-Todolist/empty%201.png?raw=true" alt="empty%201.png" />
                </div>
            )
        }else{
        return(
            filterList.map((item,index)=>{
                return(
                    <li key={index} className="todo" id={item.id}>
                        <label className="list-label">
                            <input type="checkbox" checked={item.state} onClick={e=>changeState(item.id)}/>
                            <span htmlFor="todo">{item.content}</span>
                        </label>
                        <a href="#" className="del-btn" onClick={e=>detitem(item.id)}>X</a>
                    </li>
                )}
            )
        )}
    }
    const Footer = () =>{
        return(
            <div className="list-footer">
                <p>{todo.filter(item=>item.state === '').length}個待完成項目</p>
                <input type="button" value="清除完成項目" onClick={delAll} />
            </div>
        )
    }

    function changeTab(e){
        setTabs(tabs.filter((item)=>{
            if(e.target.textContent === item.name){
                return(item.className = 'active',item);
            }else{
                return(item.className = '',item)};
        }))
    }
    function changeState(id){
        setTodo(todo.filter(item => {
            if(item.id === id){
                if(item.state === ''){return (item.state = 'checked', item) }
                else{return (item.state = '' , item)}
            }else{
                return item;
            }
        }))
    }
    function delAll(){
        setTodo(todo.filter(item=>item.state === ''))
    }

    return(
        <>
            <ul className="card tab">
                <Tabs />
            </ul>
            <div className="card content">
                <ul className="list">
                <TodoItem todo={todo} setTodo={setTodo} />
                </ul>
                <Footer />
            </div>
        </>
    )
}


const Content = () =>{
    const [todo,setTodo] = useState(todoList);
    const [addTodo,setAddTodo] = useState('');
    const [tabs, setTabs] = useState(tabsList);
    return(
        <div className="container">
            <Input todo={todo} setTodo={setTodo} addTodo={addTodo} setAddTodo={setAddTodo} tabs={tabs} setTabs={setTabs} />
            <Todo tabs={tabs} setTabs={setTabs} todo={todo} setTodo={setTodo} />
        </div>
    )
}

export default Content;